.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.page-nav {
  margin: 0 5px;
  padding: 8px 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.page-nav:hover:not(:disabled) {
  background-color: #0056b3;
}

.page-nav:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
  opacity: 0.7;
}

.page-number {
  margin: 0 5px;
  padding: 8px 12px;
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  cursor: pointer;
}

.page-number.current-page {
  background-color: #007bff;
  color: white;
}

.page-jump {
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.page-jump input {
  width: 60px;
  padding: 6px;
  margin-right: 5px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.page-jump button {
  padding: 6px 12px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.result-list {
  list-style-type: none;
  padding: 0;
}

.result-item {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #eee;
}

.result-item:last-child {
  border-bottom: none;
}

.loading, .error {
  text-align: center;
  padding: 20px;
  font-weight: bold;
}


